import React, {Component} from 'react'

class Body extends Component{
	constructor(props){
		super(props);
		this.state={
			artikel:"React adalah pustaka JavaScript open-source, front end untuk membangun antarmuka pengguna atau komponen UI. Itu dikelola oleh Facebook dan komunitas pengembang individu dan perusahaan. React dapat digunakan sebagai basis dalam pengembangan satu halaman atau aplikasi seluler. Wikipedia"
		}
			this.ubahArtikel = this.ubahArtikel.bind(this)
	}
	ubahArtikel(e) {
		e.preventDefault()
		this.setState({artikel:"React (also known as React.js or ReactJS) is an open-source, front end, JavaScript library[3] for building user interfaces or UI components. It is maintained by Facebook and a community of individual developers and companies.[4][5][6] React can be used as a base in the development of single-page or mobile applications. However, React is only concerned with rendering data to the DOM, and so creating React applications usually requires the use of additional libraries for state management and routing.[7][8] Redux[9] and React Router[10] are respective examples of such libraries."})
	}

	render(){
		return(
			<div>
				<center>
				<p>{this.state.artikel}</p><br/>
				<button onClick={(e)=>this.ubahArtikel(e)}>Next</button>
				</center>
			</div>
			)


	}

}

export default Body;