import React, {Component} from 'react';

class Isi extends Component{
	constructor(props){
		super(props)
		this.state={
			pesanDariState: 'Tugas React hari jumat'
		}
		this.ubahDataState = this.ubahDataState.bind(this)
	}

	ubahDataState(e){
		e.preventDefault()
		this.setState({pesanDariState:" Dikerjakan di hari Minggu -_-  "})
	}

	render(){
		return(
			<div>
			<p>{this.props.pesanDariProps}</p>
			<p>{this.state.pesanDariState}</p>

			<center>	
			<a href="/" onClick={(e)=>this.ubahDataState(e)}><button>Tujuan Artikel</button></a>
			</center>
			</div>
		);
	}
}



	export default Isi;